<template>
  <div class="seePage">
    <!-- 库存入库列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">三包入库管理</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看三包入库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="库存组织：" size="mini">
          <span>{{TableData.instoragList.company_name}}</span>
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          <span>{{TableData.instoragList.department_name}}</span>
        </el-form-item>
        <el-form-item label="采购员：" size="mini">
          <span>{{TableData.instoragList.buyer_name}}</span>
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          <span>{{TableData.instoragList.supplier_name}}</span>
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          <span>{{TableData.instoragList.business_time}}</span>
        </el-form-item>
        <el-form-item label="付款方式：" size="mini">
          <span>{{TableData.instoragList.receivables_mode_name}}</span>
        </el-form-item>
        <el-form-item label="仓库名称：" size="mini">
          <span>{{TableData.instoragList.warehouse_name}}</span>
        </el-form-item>
        <el-form-item label="分摊方式：" size="mini">
          <span>{{TableData.instoragList.other_price_type_name}}</span>
        </el-form-item>
        <el-form-item label="其他费用：" size="mini">
          <span>{{TableData.instoragList.other_price}}</span>
        </el-form-item>
        <el-form-item label="入库类型：" size="mini">
          <span>{{TableData.instoragList.type}}</span>
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          <span>{{TableData.instoragList.auditor_status_name}}</span>
        </el-form-item>
        <el-form-item label="摘要：" size="mini">
          <span>{{TableData.instoragList.remark}}</span>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goodsList" :summary-method="getSummaries" show-summary>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称" width="160">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" min-width="250px" label="商品编号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" min-width="160">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" :render-header="renderHeaderpro" property="production_time" width="80" label="生产日期">
        </el-table-column>
        <el-table-column property="unit_name" :show-overflow-tooltip="true" width="80px" label="单位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" min-width="140px" label="数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="abrasion_price" min-width="140px" label="磨损费" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.abrasion_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="intube_price" min-width="140px" label="内垫费" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.intube_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="change_price" min-width="140px" label="换胎收费" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.change_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" min-width="140px" label="采购金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.subtotal_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位" width="180px">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="other_price" label="其他费用" min-width="180" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.other_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" min-width="160">
        </el-table-column>
      </el-table>
      <div class="refundauditFoot">
        <span>结算金额：{{TableData.instoragList.total_price|priceFormat }}元</span>
      </div>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!-- 未审核 -->
      <template v-if="TableData.instoragList.auditor_status == 1">
        <el-button type="primary" @click="audit('auditorPassThreeInStorage')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('auditorInvalidThreeInStorage')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.instoragList.auditor_status == 3 || TableData.instoragList.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "inventoryThreeinstorageThreeinstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        instoragList: {},
        goodsList: []
      }
    };
  },
  computed: { ...mapGetters(["threeinstorageSeeGet"]) },
  methods: {
    ...mapActions(["threeinstorageIndexACT"]),
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            生产日期
          </span>
        </el-tooltip>
      );
    },
    goBack() {
      this.threeinstorageIndexACT({
        goPage: this.threeinstorageSeeGet.goPage,
        params: this.threeinstorageSeeGet.params,
        pageSize: this.threeinstorageSeeGet.pageSize
      });
      this.$router.push({
        path: "/inventory/threeinstorage"
      });
    },
    audit(api) {
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 5,
          bill_id: this.instorage_id,
          operation_type: api === "auditorPassThreeInStorage" ? 4 : 6
        })
        .then(() => {
          this.$axios({
            method: "post",
            url: this.$api[api],
            data: {
              instorage_id: this.instorage_id
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message.success(res.data.msg || res.data.data);
              this.goBack();
            }
          });
        });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }

        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 6) {
            sums[index] = parseFloat(sums[index]);
          } else if ([7, 8, 9, 10, 12].includes(index)) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            if (index === 11) {
              this.$set(this.params, "total_price", sums[index]);
            }
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  mounted() {
    /** 入库详情 */
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.instorage_id = this.threeinstorageSeeGet.instorage_id;
    this.$axios({
      method: "get",
      url: this.$api.infoThreeInStorage,
      params: {
        instorage_id: this.instorage_id
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.TableData.instoragList = res.data.data.instoragList;
        this.TableData.goodsList = res.data.data.goodsList;
      }
    });
  }
};
</script>

